<template>
	<div class="myquestions-container">
		<div class="myquestions-header">
			<div class="myquestions-zhong">
				<div class="myquestions-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="myquestions-ti">我的提问</div>
				<div></div>
			</div>
		</div>
		<div class="myquestions-content">
			<div class="myquestions-vacancy" v-show="discussion_list.length==0">暂无数据</div>
			<div v-for="item in discussion_list"  @click="go_response(item.discussion_id)">
				<div class="myquestions-shang">{{item.discussion_title}}</div>
				<div class="myquestions-bj">
					<div class="myquestions-wenz">{{item.discussion_content}}</div>
					<!--<div class="myquestions-wap">
						<div class="myquestions-around" v-for="one in item.img_list">
							<div class="myquestions-together">
								<div class="myquestions-tu" @click="openSimpleDialog(one)">
									<img :src="baseUrl+one">
								</div>
							</div>
						</div>
					</div>-->
					<div class="myquestions-wap">
						<div class="myquestions-around" v-for="(one,index) in item.img_list" :key="index">
							<div class="myquestions-tupian" @click="openSimpleDialog(item.img_list)">
								<img :src="baseUrl+one">
							</div>
						</div>
					</div>
					<div class="myquestions-between">
						<div class="myquestions-flex">
							<div class="myquestions-niming" v-if="item.discussion_anonymity==0">匿名</div>
							<div class="myquestions-niming" v-else-if="item.discussion_anonymity==1">{{item.discussion_user_id}}</div>
							<!--<div class="myquestions-niming">匿名</div>-->
							<div class="myquestions-shijian">{{$root.formatDate(item.create_time)}}</div>
						</div>
						<div class="myquestions-flex">
							<div class="myquestions-xiaoxi">
								<img src="../assets/icon_pinglun.png">
							</div>
							<div class="myquestions-shuliang">{{item.discussion_count}}</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<mu-container>
		  <mu-dialog width="100%" :open.sync="openSimple">
		  	<div class="myquestions-lun">
		  		<mu-carousel interval='80000'>
					<mu-carousel-item v-for="(one,index) in carousel_img_list" :key="index">
						<img :src="baseUrl+one"  @click="openBotttomSheet(one)">
					</mu-carousel-item>
				</mu-carousel>
		  	</div>
		  	<!--<div class="myquestions-tantu">
		  		<img :src="!!img_url?baseUrl+img_url:'../assets/img_tl1.png'">
		  	</div>-->
		  </mu-dialog>
		</mu-container>
		<mu-bottom-sheet :open.sync="open">
	   		 <mu-list @item-click="closeBottomSheet">
	      		<mu-sub-header>
	      			<div class="myquestions-between">
	      				<div class="myquestions-flex" @click="save()">
		      				<div class="myquestions-xiazai">
		      					<img src="../assets/xiazai.png">
		      				</div>
		      				<div class="myquestions-baocun">保存</div>
		      			</div>
		      			<div class="myquestions-qux" @click=" closeSimpleDialog()">取消</div>
	      			</div>
	      		</mu-sub-header>
	    	</mu-list>
		</mu-bottom-sheet>
		<div class="myquestions-footer">

		</div>
	</div>
</template>

<script>
	import Qs from 'qs'
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				user_info: {},
				userid: '',
				discussion_list: [],
				data: [],
				openSimple: false,
				open: false,
				img_url: '',
				discussion_pic: [],
				carousel_img_list: []
			}
		},
		methods: {
			openSimpleDialog (img_list) {
				this.carousel_img_list = img_list;
		      	this.openSimple = true;
		    },
		    closeSimpleDialog () {
		      this.openSimple = false;
		      this.closeBottomSheet()
		    },
		    closeBottomSheet () {
		      this.open = false;
		    },
		    openBotttomSheet (url) {
		    	this.img_url = url;
		      this.open = true;
		    },
			back() {
				window.history.back();
			},
			go_response(id) {
				this.$router.push('/response/' + id)
			},
			discussion() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
					//					discussionid: that.discussionid
				};
				that.$http.post('/discussion/sel_userdiscussion', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.discussion_list = res.data;
						that.discussion_list = that.discussion_list.reverse();
						that.discussion_list.forEach(function(item){ //循环处理每一条数据
							item.img_list = []; //在每一条数据里面 定义一个 空的 数组  用来下面放图片路径
							if (!!item.discussion_pic) { //判断 discussion_pic 字段有值 才去处理  要不然会报错
								if (item.discussion_pic.indexOf(',') >=0) { 
									//判断discussion_pic字段里面有没有，如果没有，那就只有一条数据 不用去裁取
									item.img_list = item.discussion_pic.split(',');
								}else{
									// 只有一条数据 直接插入到img_list里面
									item.img_list.push(item.discussion_pic);
								}
							}

						})
					}
				})
			},
			//			保存图片
			save(img_url){
				var that = this
				dsBridge.call('saveImg',that.baseUrl+that.img_url);
			}
		},
		created() {
			// 页面刚加载的时候执行
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
			this.discussion();
		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.myquestions-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.myquestions-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		border-bottom: 1px solid #eaeaea;
	}
	.myquestions-vacancy{
		font-size: 1.5rem;
		color: #999999;
		text-align: center;
		padding-top: 2rem;
	}
	.myquestions-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.myquestions-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.myquestions-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	
	.myquestions-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.myquestions-shang {
		font-size: 1.8rem;
		font-weight: 600;
		color: #000000;
		background: #FFFFFF;
		padding: 1rem 0 0.5rem 1.5rem;
		border-bottom: 1px solid #EAEAEA;
	}
	
	.myquestions-bj {
		background: #FFFFFF;
		margin-bottom: 1rem;
	}
	
	.myquestions-wenz {
		font-size: 1.4rem;
		color: #8e8c8c;
		padding: 0.3rem 2.6rem 1.2rem 1.5rem;
	}
	/*.myquestions-wap{
		display: flex;
		flex-wrap: wrap;
	}
	.myquestions-around {
		display: flex;
		justify-content: space-around;
		width: 100%;
	}
	.myquestions-together{
		width: 30%;
	}
	.myquestions-tu img {
		width: 100%;
		height: 10rem;
		margin-bottom: 1rem;
		margin-right: 0.5rem;
	}*/
	
	.myquestions-between {
		display: flex;
		justify-content: space-between;
	}
	
	.myquestions-flex {
		display: flex;
	}
	
	.myquestions-niming {
		font-size: 1.2rem;
		color: #8A8A8A;
		padding-left: 1.5rem;
		padding-right: 1rem;
	}
	
	.myquestions-shijian {
		font-size: 1.2rem;
		color: #8A8A8A;
	}
	
	.myquestions-xiaoxi img {
		width: auto;
		height: 1.2rem;
	}
	
	.myquestions-shuliang {
		font-size: 1.2rem;
		color: #8A8A8A;
		padding: 0 1rem 1.1rem 1rem;
	}
	.myquestions-tantu{
		margin-bottom: -1rem;
	}
	.myquestions-tantu img{
		width: 100%;
		height: auto;
		margin: 0 auto;
	}
	.myquestions-xiazai img{
		height: 2rem;
		width: auto;
		margin-top: 1rem;
		margin-right: 1rem;
	}
	.myquestions-baocun{
		font-size: 1.5rem;
		color: #000;
		/*text-align: center;*/
	}
	.myquestions-qux{
		font-size: 1.5rem;
		padding-right: 1.5rem;
		color: #000000;
	}
	/deep/ .mu-dialog-body{
		padding: 0;
	}
	/deep/ .mu-dialog{
		-webkit-box-shadow: none;
		box-shadow: none;
	}
	.myquestions-wap{
		display: flex;
		flex-wrap: wrap;
	}
	.myquestions-around  {
		display: flex;
		justify-content: space-around;
		width: 33.33%;
		/*padding-top: 1rem;*/
		padding-bottom: 1rem;
	}
	.myquestions-tupian img{
		width: 100%;
		height: 10rem;
		padding: 0 0.5rem;
		border-radius: 15px;
	}
	/*轮播图*/
	.myquestions-lun{
		width: 100%;
		height: 100%;
		position: relative;
	}
	.myquestions-lun img {
		width: 100%;
		/*height: 100%;*/
		height: auto;
		margin: 0 auto;
	}
	/deep/ .mu-carousel {
		width: 100%;
		height: 75rem;
	}
	/deep/ .mu-tab {
		min-width: 0;
	}
	/deep/ .mu-carousel-indicator-icon {
		background: #fe4563;
	}
	
	/deep/ .mu-carousel-button.mu-icon-button {
		font-size: 0;
		z-index: 0;
	}
	/deep/ .mu-dialog{
		max-width : 100%;
		background: #000000;
		-webkit-box-shadow: none;
		box-shadow: none;
	}
</style>


